@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin desktop {
  @media (max-width: $vp-1366 - 1) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: $vp-1200 - 1) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

